<script>
import Vue from 'vue'
Vue.use(require('vue-moment'));
import axios from "axios"

import { showToast } from '@/assets/js/index';
export default {
    name:"Profile",
    data() {
        return { 
            info:{country:{},state:{},},
            user_id:localStorage.getItem('user_id'),
            loading:{
                main:false,
            }
        }
    },
    methods:{
        getUserData() {
            this.loading.main = true;
 
            axios.get(`/admin/v1/users/${this.user_id}`)
            .then(res=>{
                this.info = res.data.data;
                this.loading.main = false
            })
            .catch(()=>{
                this.loading.main = false
            })
        },

        updateInfo() {
            let data = {
                first_name:this.info.first_name,
                last_name:this.info.last_name,
                phone:this.info.phone,
                email:this.info.email,
                alias:1
            }

            axios.put(`/admin/v1/users/${this.user_id}`, data)
            .then(res=>{
                console.log(res)
                showToast('success', 'Success', 'User has been updated successfully!', this)
            })
            .catch(err=>{
                this.loading.submit = false;
                for(let error in err.response.data.errors) {
                    showToast('danger', `${error}`, `${err.response.data.errors[error][0]}`, this)
                }
                console.log(err.response)
            })
        }
    },
    mounted() {
        this.getUserData();


    }
}
</script>
